* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
}

:root {
  --manrope-normal: "Manrope Normal";
  --manrope-bold: "Manrope Bold";
  --very-dark-grayish-blue: #48556a;
  --desaturated-dark-blue: #6d7f97;
  --grayish-blue: #9eafc2;
  --light-grayish-blue: #ecf2f8;
  --white: #fff;
  --main-bg: var(--light-grayish-blue);
  --article-bg: var(--white);
  --font-bold: var(--manrope-bold);
  --font-medium: var(--manrope-normal);
  --article-headings-color: var(--very-dark-grayish-blue);
  --article-text-color: var(--desaturated-dark-blue);
  --article-date-color: var(--grayish-blue);
}

@font-face {
  font-family: Manrope Normal;
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("https://fonts.gstatic.com/s/manrope/v15/xn7_YHE41ni1AdIRqAuZuw1Bx9mbZk7PFN_C-bk.woff2") format("woff2");
  unicode-range: U+??, U+131, U+152-153, U+2BB-2BC, U+2C6, U+2DA, U+2DC, U+304, U+308, U+329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

@font-face {
  font-family: Manrope Bold;
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("https://fonts.gstatic.com/s/manrope/v15/xn7_YHE41ni1AdIRqAuZuw1Bx9mbZk4aE9_C-bk.woff2") format("woff2");
  unicode-range: U+??, U+131, U+152-153, U+2BB-2BC, U+2C6, U+2DA, U+2DC, U+304, U+308, U+329, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

.display-none {
  display: none;
}

.display-grid {
  display: grid;
}

.h1 {
  font-family: var(--font-bold);
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 1.5;
}

@media (width >= 960px) {
  .h1 {
    font-size: 2rem;
    line-height: 1.4;
  }
}

.h2 {
  font-family: var(--font-bold);
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 1.54;
}

.p {
  font-family: var(--font-medium);
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.54;
}

@keyframes opacity-zero-to-full {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.article {
  background: var(--article-bg);
  border-radius: 1rem;
  grid-template-rows: 20rem;
  grid-template-columns: minmax(60ch, 32.7rem);
  display: grid;
  overflow: hidden;
  box-shadow: 0 40px 40px -10px #c9d5e180;
}

.article__header > img, .article__avatar > img {
  object-fit: cover;
  background-color: #000;
  block-size: 100%;
  inline-size: 100%;
}

.article__content {
  flex-direction: column;
  gap: 1.2rem;
  padding: 3.6rem 3.2rem 2rem;
  display: flex;
}

.article__title {
  color: var(--article-headings-color);
}

.article__text {
  color: var(--article-text-color);
  max-inline-size: 31ch;
}

.article__info {
  grid-area: 1 / 1;
  grid-template-columns: 1fr 3.2rem;
  align-items: center;
  padding: 1.6rem 3.2rem;
}

.article__share {
  background-color: var(--very-dark-grayish-blue);
  grid-area: 1 / 2;
  grid-template-columns: 1fr 3.2rem;
  align-items: center;
  padding: 2rem 3.2rem;
}

.article__author-date {
  grid-template-columns: 4rem 17.5rem;
  grid-auto-rows: 2rem;
  gap: 0 1.6rem;
  display: grid;
}

.article__avatar {
  border-radius: 50%;
  grid-area: 1 / 1 / span 2;
  overflow: hidden;
}

.article__author {
  color: var(--article-headings-color);
  grid-area: 1 / 2;
}

.article__date {
  color: var(--article-date-color);
  grid-area: 2 / 2;
}

.article__popover {
  background-color: #0000;
  border: none;
  outline: none;
  animation-name: opacity-zero-to-full;
  animation-duration: .6s;
  animation-timing-function: ease;
  animation-iteration-count: 1;
}

.article__footer {
  grid-template-columns: 100% 0;
  transition: all .5s;
  display: grid;
}

.article__footer--share {
  grid-template-columns: 0 100%;
}

@media (width >= 960px) {
  .article {
    grid-template-rows: 20.8rem 7.2rem;
    grid-template-columns: 28.5rem 44.5rem;
  }

  .article__header {
    grid-area: 1 / 1 / span 2;
  }

  .article__content {
    grid-area: 1 / 2;
    padding: 3.2rem 4rem 2rem;
  }

  .article__title {
    max-inline-size: 26ch;
  }

  .article__text {
    max-inline-size: 50ch;
  }

  .article__info {
    padding-block: 0 3.2rem;
  }

  .article__footer {
    grid-area: 2 / 2;
  }
}

.button-share {
  background-color: var(--light-grayish-blue);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  width: 3.2rem;
  height: 3.2rem;
  display: flex;
}

.button-share:after {
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='15' height='13' fill='none'%3e%3cpath fill='%236E8098' d='M15 6.495 8.766.014V3.88H7.441C3.33 3.88 0 7.039 0 10.936v2.049l.589-.612C2.59 10.294 5.422 9.11 8.39 9.11h.375v3.867L15 6.495Z'/%3e%3c/svg%3e");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  width: 100%;
  height: 100%;
}

.button-share--active {
  background-color: var(--desaturated-dark-blue);
}

.button-share--active:after {
  filter: brightness(5);
}

.share {
  align-items: center;
  gap: 2rem;
  height: 100%;
  display: inline-flex;
}

.share__title {
  font-family: var(--font-medium);
  letter-spacing: .38em;
  color: var(--grayish-blue);
  text-transform: uppercase;
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.54;
}

.share__list {
  gap: 1.6rem;
  list-style-type: none;
  display: flex;
}

.share__item {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 2rem;
  height: 2rem;
}

