  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='20' height='20'%3e%3cpath fill='%23FFF' d='M10 0C4.478 0 0 4.477 0 10c0 4.237 2.636 7.855 6.356 9.312-.088-.791-.167-2.005.035-2.868.182-.78 1.172-4.97 1.172-4.97s-.299-.6-.299-1.486c0-1.39.806-2.428 1.81-2.428.852 0 1.264.64 1.264 1.408 0 .858-.545 2.14-.828 3.33-.236.995.5 1.807 1.48 1.807 1.778 0 3.144-1.874 3.144-4.58 0-2.393-1.72-4.068-4.177-4.068-2.845 0-4.515 2.135-4.515 4.34 0 .859.331 1.781.745 2.281a.3.3 0 0 1 .069.288l-.278 1.133c-.044.183-.145.223-.335.134-1.249-.581-2.03-2.407-2.03-3.874 0-3.154 2.292-6.052 6.608-6.052 3.469 0 6.165 2.473 6.165 5.776 0 3.447-2.173 6.22-5.19 6.22-1.013 0-1.965-.525-2.291-1.148l-.623 2.378c-.226.869-.835 1.958-1.244 2.621.937.29 1.931.446 2.962.446 5.522 0 10-4.477 10-10S15.522 0 10 0z'/%3e%3c/svg%3e");
}

.popover {
  grid-template-rows: 5.5rem auto;
  grid-template-columns: 24.8rem;
  justify-content: center;
  display: grid;
}

.popover__content {
  background-color: var(--very-dark-grayish-blue);
  border-radius: 1rem;
  grid-area: 1 / 1 / span 1;
  justify-content: center;
  display: flex;
}

.popover:after {
  content: "";
  border-style: solid;
  border-width: 1.2rem;
  border-color: var(--very-dark-grayish-blue) transparent transparent transparent;
  grid-area: 2 / 1 / span 1;
  justify-self: center;
}

.main {
  background-color: var(--main-bg);
  place-items: center;
  min-block-size: 100dvh;
  inline-size: 100vw;
  padding: 0 2.4rem;
  display: grid;
}
